@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: lightgray;
  color: white;
  font-family: "Roboto", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 2rem;
}

.board {
  border: 1rem solid #6b5d58;
  background: #fafafa;
  width: 600px;
  height: 600px;
  display: grid;
  align-self: flex-start;
  /* grid-template-rows: repeat(9, 1fr);
  grid-template-columns: repeat(9, 1fr); */
  /* gap: 0.5rem; */
}

.squares:nth-child(21n) {
  background-color: #ffbebe;
}
.squares:nth-child(21n + 1) {
  background-color: #ccffd5;
}
.squares:nth-child(21n + 2) {
  background-color: #fffecc;
}
.squares:nth-child(21n + 3) {
  background-color: #ccffff;
}
.squares:nth-child(21n + 4) {
  background-color: #ccd5ff;
}
.squares:nth-child(21n + 5) {
  background-color: #f7ccff;
}
.squares:nth-child(21n + 6) {
  background-color: #ffccd7;
}
.squares:nth-child(21n + 7) {
  background-color: #b0b1af;
}
.squares:nth-child(21n + 8) {
  background-color: #ffe9cc;
}
.squares:nth-child(21n + 9) {
  background-color: #ffbebe;
}
.squares:nth-child(21n + 10) {
  background-color: #a7f7b6;
}
.squares:nth-child(21n + 11) {
  background-color: #f7f59f;
}
.squares:nth-child(21n + 12) {
  background-color: #a4fbfb;
}
.squares:nth-child(21n + 13) {
  background-color: #8592ce;
}
.squares:nth-child(21n + 14) {
  background-color: #f1a1ff;
}
.squares:nth-child(21n + 15) {
  background-color: #ffcad5;
}
.squares:nth-child(21n + 16) {
  background-color: #c9e0b2;
}
.squares:nth-child(21n + 17) {
  background-color: #ffd093;
}
.squares:nth-child(21n + 18) {
  background-color: #a3a3a3;
}
.squares:nth-child(21n + 19) {
  background-color: #e9d1ff;
}
.squares:nth-child(21n + 20) {
  background-color: #feffd6;
}

.squares {
  /* background: hsl(0, 1%, 35%); */
  border: none;
  font-size: 3vw;
  font-weight: 600;
  cursor: pointer;
  outline: none;
}

.X {
  color: #ff0000;
}
.O {
  color: #00b7ff;
}

.info-wrapper {
  display: grid;
  justify-content: space-between;
}

h3 {
  padding: 1rem;
}

li {
  list-style: none;
}

.info-wrapper {
  display: grid;
  grid-template-columns: 75% 25%;
  grid-template-rows: auto;
  grid-template-areas:
    "history win"
    "slid win";
}

.select-container .h3 {
  grid-area: history;
}

.select-container .h2 {
  grid-area: win;
}

.select-container .input {
  grid-area: slid;
}

.slidecontainer {
  width: 100%;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  background: #292929;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #4caf50;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #4caf50;
  cursor: pointer;
}

.button {
  background-color: #000000;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  /* float: right; */
}

.select-container h3 {
  text-align: center;
}

.select-container {
  display: grid;
}
